@media (max-width: @screen-xs-max) {
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h6, .h6 {
    line-height: @headings-line-height-mobile;
  }

  h1, .h1 { font-size: @font-size-h1-mobile; }
  h2, .h2 { font-size: @font-size-h2-mobile; }
  h3, .h3 { font-size: @font-size-h3-mobile; }
  h4, .h4 { font-size: @font-size-h4-mobile; }
}

h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: @line-height-computed;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
p,
ul {
  margin-bottom: @line-height-computed;
}

p.lead {
  font-size: @font-size-base;
  line-height: inherit;
  font-weight: inherit;

  @media (min-width: @screen-sm-min) {
    font-size: @font-size-large;
    line-height: (32 / 20);
  }
}

blockquote {
  color: @gray-darker;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 20px;
  border-left-width: 2px;
  font-style: italic;
  font-size: @blockquote-font-size-mobile;
  margin-top: floor(@line-height-computed * 2);
  margin-bottom: floor(@line-height-computed * 2);

  @media (min-width: @screen-sm-min) {
    margin-top: floor(@line-height-computed * 2.3);
    margin-bottom: floor(@line-height-computed * 2.3);
    font-size: @blockquote-font-size;
    padding-left: 60px;
  }
}

a {
  text-decoration: underline;
  transition: color .2s ease;
}

ul {
  margin-left: -20px;

  li {
    padding-left: 6px;
    margin-bottom: 4px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

figcaption {
  font-size: @font-size-small-mobile;
  margin-top: @line-height-computed-mobile;
  margin-bottom: floor(@line-height-computed-mobile / 2);
  padding: 0 @line-height-computed-mobile;
  display: block;

  @media (min-width: @screen-sm-min) {
    font-size: @font-size-small;
    margin-top: @line-height-computed;
    margin-bottom: floor(@line-height-computed / 2);
    padding: 0 @line-height-computed;
  }
}
